import React from 'react';
import {TiBeaker} from 'react-icons/ti';
import Job from './Job';

import Context from '../../Context';

export default () => (
  <Context.Consumer>
    {ctx => (
      <div className="right-section">
        <div className="head">
          <div className="icon">
            <TiBeaker />
          </div>
          <div className="label">Experience</div>
        </div>
        <div className="rs-body">
          {ctx.experience.map ((itm, idx) => (
            <Job job={itm} key={`job-${idx}`} />
          ))}
        </div>
      </div>
    )}
  </Context.Consumer>
);



