import React from 'react';
import {TiSocialLinkedin, TiSocialGithub, TiWorld} from 'react-icons/ti';
import Context from '../../Context';

export default () => (
  <Context.Consumer>
    {ctx => (
      <div className="intro">
        <div className="me">
          <div className="name"><div>Youssef CHIHI</div></div>
          <div className="profession"><div>{ctx.experience[0].title}</div></div>
        </div>
        <div className="social-links">
          <a
            href="https://www.linkedin.com/in/youssef-chihi"
            className="social"
            target="_blank"
          >
            <div className="icon">
              <TiSocialLinkedin />
            </div>
            <div className="label">/in/youssef-chihi</div>
          </a>
          <a
            href="https://github.com/YoucefChihi"
            className="social"
            target="_blank"
          >
            <div className="icon">
              <TiSocialGithub />
            </div>
            <div className="label">/YoucefChihi</div>
          </a>
          <a href="https://youcefchihi.github.io" className="social">
            <div className="icon">
              <TiWorld />
            </div>
            <div className="label">youcefchihi.github.io</div>
          </a>
        </div>
      </div>
    )}
  </Context.Consumer>
);



