import React from 'react';
import {TiCode} from 'react-icons/ti';

export default ({data}) => (
  <div className="sub-section skills">
    <div className="head">
      <div className="icon"><TiCode /></div>
      <div className="label">Skills</div>
    </div>
    <div className="body">
      {data.map ((itm, idx) => (
        <div className="item" key={`skill-${idx}`}>
          <div className="icon">{itm.icon}</div>
          <div className="label">{itm.label}</div>
        </div>
      ))}
    </div>
  </div>
);



