import React from 'react';
import {TiLocation, TiMail, TiPhone} from 'react-icons/ti';

export default () => (
  <div className="contact-info">
    <div className="contact">
      <div className="icon">
        <TiLocation />
      </div>
      <div className="label">Tunis, Tunisia</div>
    </div>
    <div className="contact">
      <div className="icon">
        <TiPhone />
      </div>
      <div className="label">(+216) 25 757 103</div>
    </div>
    <a
      href="mailto:youcef.chihi@gmail.com?Subject=Hello"
      target="_top"
      className="contact"
    >
      <div className="icon">
        <TiMail />
      </div>
      <div className="label">youcef.chihi@gmail.com</div>
    </a>
  </div>
);



