import React from 'react';
import {TiLightbulb, TiArrowUp} from 'react-icons/ti';

export default ({data}) => (
  <div className="sub-section projects">
    <div className="head">
      <div className="icon"><TiLightbulb /></div>
      <div className="label">Projects</div>
    </div>
    <div className="body">
      {data.map ((itm, idx) => (
        <a
          href={itm.url}
          key={`project-${idx}`}
          className="project"
          target="_blank"
        >
          <div className="title">
            {itm.title}
            <div className="icon">
              <div className="border" />
              <div className="arrow">
                <TiArrowUp />
              </div>
            </div>
          </div>
          <div className="description">
            {itm.description}
          </div>
        </a>
      ))}
    </div>
  </div>
);



